





















import { Component, Mixins, Prop } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
import getRepository from '@/services'
import EmployeeRepository from '@/services/repositories/employee'

const beUser = namespace('beUser')
@Component({ components: {} })
export default class AcceptCreateAcount extends Mixins(PopupMixin) {
  @Prop({ default: [] }) listIdUser?: Array<Record<string, any>>
  @beUser.State('user') user?: Record<string, any>
  api!: EmployeeRepository
  ids: any = []
  params: any = {}
  handleClose(): void {
    this.setOpenPopup({
      popupName: 'acceptCreateAcount',
      isOpen: false
    })
  }
  handleOpen(): void {
    this.ids = this.listIdUser
  }
  handleSave(): void {
    const params = {
      ids: this.ids
    }
    this.api
      .createMutipleUser(params)
      .then(() => {
        this.$message.success('Tạo tài khoản thành công !')
        this.setOpenPopup({
          popupName: 'acceptCreateAcount',
          isOpen: false
        })
      })
      .catch((err: any) => this.$message.error(err.message))
    this.$emit('save')
  }
  created(): void {
    this.api = getRepository('employee', {
      districtId: this.user?.districts?.[0].id
    })
  }
}
