import getRepository from '@/services'
import EmployeeRepository from '@/services/repositories/employee'
import UserRepository from '@/services/repositories/user'
import { Component, Watch } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
import UserPermission from '../components/popup/UserPermission.vue'
import InfoEmployee from '../components/popup/InfoEmployee.vue'
import LocationRepository from '@/services/repositories/location'
import { forEach, trim, debounce } from 'lodash'
import SearchBar from '../../audit/components/filters/Searchbar.vue'
import PopupAcceptCreateAcount from '../components/popup/PopupAcceptCreateAcount.vue'

import PermissionRoleRepository from '@/services/repositories/permissionRole'

const employee = namespace('beEmployee')
const beUser = namespace('beUser')

@Component({ components: { UserPermission, InfoEmployee, SearchBar, PopupAcceptCreateAcount } })
export default class Employee extends PopupMixin {
  @beUser.State('role') roleMembers!: any
  @beUser.State('user') user!: any
  @employee.State('totalMembers') totalMembers!: number | string
  @employee.Action('setMembers') setMembers!: (payLoad: Array<Record<string, unknown>>) => void
  @employee.Action('setTotalMembers') setTotalMembers!: (payload: any) => void
  api!: EmployeeRepository
  apiUser!: UserRepository
  apiPermission!: PermissionRoleRepository
  isOpenFilter = false
  loading = false
  isLoading = false
  isActive = true
  isConflickClick = false
  conflict = false
  listTitle = []
  // transOfficeIdArray = []
  transOfficeIdArray = ''
  transactionList: any = []
  members: Array<Record<string, unknown>> = []
  memberInfo: Record<string, unknown> = {}
  apiLocation!: LocationRepository
  location: Array<Record<string, unknown>> = []
  locationList = []
  memberDetail: any
  roleMember: any
  dataList: any
  role: any
  idUser = 0
  form = {
    region: '',
    sort: '',
    unitShow: '',
    levelEmployee: ''
  }
  params = {
    parentId: ''
  }
  paginitInfo = {
    // i: 0,
    // ps: 20,
    limit: 20,
    page: 1,
    search: '',
    sort: 1,
    locationId: '',
    statusAccount: 0,
    // groupTitle: '',
    total: this.totalMembers
  }
  // handleRowClick(one: Record<string, any>): void {
  //   this.setOpenPopup({
  //     popupName: 'infoemployee-popup',
  //     isOpen: true
  //   })
  // }

  handleClickSearch(): void {
    this.paginitInfo.locationId = this.transOfficeIdArray
    this.paginitInfo.statusAccount = +this.form.region
    // this.paginitInfo.groupTitle = this.form.levelEmployee
    this.paginitInfo.sort = +this.form.sort
    this.paginitInfo.page = 1
    this.getDataMembers()
    this.getTotalMembers()
    this.isOpenFilter = false
  }
  getBranchList(params: Record<string, any>): void {
    this.apiLocation
      ?.getLocation({
        params
      })
      .then(list => (this.locationList = list))
      .catch(error => console.log(error))
  }
  handleClickReset(): void {
    this.form.region = '0'
    this.form.sort = '1'
    this.transOfficeIdArray = ''
    this.form.levelEmployee = ''
    this.paginitInfo.statusAccount = +this.form.region
    this.paginitInfo.sort = +this.form.sort
    this.paginitInfo.locationId = this.transOfficeIdArray
    this.getDataMembers()
    this.getTotalMembers()
  }
  getOfficeList(): void {
    const temp = {
      search: '',
      type: 3,
      i: 0,
      districtId: this.user?.districts?.[0].id,
      ps: ''
    }
    this.apiUser
      .getAreaList(temp)
      .then((res: any) => {
        this.transactionList = res.data
        this.isLoading = false
      })
      .catch(() => {
        this.isLoading = false
      })
  }

  pageInfo = {
    page: 1,
    limit: 10,
    total: 1000
  }
  searchValue = ''

  //code moi
  // handleSelectPermissions(row: Record<string, any>): void {

  // }
  typeCreate = ''
  handlePermissions(row: Record<string, any>, id: number): void {
    // alert(roleMembers.userRole)
    this.isConflickClick = true
    this.memberInfo = row
    this.idUser = 0
    this.typeCreate = 'create'
    if (id) {
      this.idUser = id
      if (!row.email) {
        this.$message.error(`Nhân viên ${row.fullName} không có email nên không tạo được tài khoản !`)
      } else {
        this.setOpenPopup({
          popupName: 'userPermission',
          isOpen: true
        })
      }
    }
  }
  handlePermissionsnew(row: Record<string, any>): void {
    // alert(roleMembers.userRole)
    this.isConflickClick = true
    this.memberInfo = row
    this.idUser = 0
    this.setOpenPopup({
      popupName: 'userPermission',
      isOpen: true
    })
  }
  checkR = false
  checkRole(listRole: Record<string, any>): void {
    // listRole.includes(this.roleMembers === 'ADMIN') ? false : true
    if (listRole.includes('ADMIN')) {
      this.checkR = true
    } else {
      this.checkR = false
    }
    console.log('check', this.checkR)
  }
  handleRowClick(row: Record<string, any>): void {
    if (this.isConflickClick) {
      this.isConflickClick = false
      return
    }
    this.memberInfo = row.row
    this.setOpenPopup({
      popupName: 'infoemployee-popup',
      isOpen: true
    })
  }
  // handleSelectPermission(data: Record<string, unknown>, id: any): void {
  //   this.idUser = 0
  //   if (id) {
  //     this.idUser = id
  //   }
  //   this.memberInfo = data
  //   this.setOpenPopup({
  //     popupName: 'infoemployee-popup',
  //     isOpen: true
  //   })
  // }
  //end code moi
  handleFilter(): void {
    this.paginitInfo.page = 1
    this.getDataMembers()
    this.getTotalMembers()
  }
  @Watch('searchValue', { immediate: false })
  searchText(value: string): void {
    this.search(value)
  }
  search = debounce((value: string) => {
    this.handleSearch(value)
  }, 500)
  handleSearch(value: string): void {
    this.loading = true
    this.paginitInfo.page = 1
    this.paginitInfo.search = trim(value)
    this.getDataMembers()
    this.getTotalMembers()
  }

  renderTotal(): number | string {
    return this.totalMembers
  }
  handleChangeSize(e: number): void {
    this.paginitInfo.limit = e
    this.getDataMembers()
    this.getTotalMembers()
  }
  handleCurrentChange(e: number): void {
    this.loading = true
    this.paginitInfo.page = e
    this.getDataMembers()
  }
  renderIndex(index: number): number {
    return (this.paginitInfo.page - 1) * this.paginitInfo.limit + index + 1
  }
  created(): void {
    this.checkRole(this.roleMembers.userRole)
    this.loading = true
    this.form.region = '0'
    this.form.sort = '1'
    this.api = getRepository('employee', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiLocation = getRepository('location', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiUser = getRepository('user', {
      districtId: this.user?.districts?.[0].id
    })
    this.getDataMembers()
    this.getTotalMembers()
    this.getOfficeList()
  }
  getDataMembers(): void {
    this.loading = true
    this.api
      .all(this.paginitInfo)
      .then(data => {
        this.members = data
        this.loading = false
      })
      .catch(e => {
        console.log(e)
      })
  }
  mounted(): void {
    // this.role = this.beUser
    this.getBranchList(this.params)

    // this.getOfficeList()
  }
  getTotalMembers(): void {
    this.loading = true
    this.api
      .getCountMembers(this.paginitInfo)
      .then(data => {
        this.loading = false
        this.setTotalMembers(data)
        this.paginitInfo.total = data
      })
      .catch(error => {
        console.log(error)
      })
  }
  remoteTransaction(query: string): void {
    if (query == '') {
      this.apiUser
        ?.getAreaList({
          search: '',
          type: 3,
          i: 0,
          districtId: this.user?.districts?.[0].id,
          ps: 10
        })
        .then(list => (this.transactionList = list.data))
        .catch(error => console.log(error))
    } else {
      this.apiUser
        ?.getAreaList({
          search: trim(query),
          type: 3,
          i: 0,
          districtId: this.user?.districts?.[0].id,
          ps: 10
        })
        .then(list => (this.transactionList = list.data))
        .catch(error => console.log(error))
    }
  }
  isOpenBtnAddUser = false
  userIds: Array<number> = []
  arrayStatusAccount: Array<number> = []
  arrayEmail: Array<number> = []
  notificationMessage = ''
  handleSelection(value: Array<Record<string, any>>): void {
    this.resetItem()
    if (value.length > 0) {
      forEach(value, user => {
        this.userIds.push(user.id)
        this.arrayStatusAccount.push(user.statusAccount)
        this.arrayEmail.push(user.email)
      })
      //@ts-ignore
      if (this.arrayStatusAccount.includes('ACTIVE') === false) {
        this.isOpenBtnAddUser = true
        forEach(this.arrayEmail, email => {
          if (!email) {
            this.notificationMessage = 'Tồn tại nhân viên không có email nên không tạo được tài khoản'
          }
        })
      }
    }
  }
  resetItem(): void {
    this.notificationMessage = ''
    this.isOpenBtnAddUser = false
    this.userIds = []
    this.arrayStatusAccount = []
    this.arrayEmail = []
  }
  createUser(ids: number[]): void {
    // alert('toang')
    if (this.notificationMessage) {
      this.$message.error(this.notificationMessage)
    } else {
      console.log(ids)
      // const params = {
      //   ids: ids
      // }
      this.setOpenPopup({
        popupName: 'acceptCreateAcount',
        isOpen: true
      })
    }
  }
}
