






























































































import getRepository from '@/services'
import { Component, Prop, Vue } from 'vue-property-decorator'
import InfoEmployeeRepository from '@/services/repositories/employee/info-index'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'

const beUser = namespace('beUser')

@Component
export default class UserInformation extends PopupMixin {
  @beUser.State('user') user!: any
  @Prop({ required: false, type: Object, default: {} }) memberInfor!: Record<string, any>
  permissionMember: any
  permissionRole: any
  adminStaff = false
  adminAccount = false
  formCertCode = false
  updateRole = []
  fullName = ''
  email = ''
  phone = ''
  avatar = ''
  titleName = ''
  locationPathList = ''
  birthday = ''
  gender = ''
  memberNo = ''
  joinDate = ''
  certCode = ''

  api!: InfoEmployeeRepository
  apiupdateCertCode!: InfoEmployeeRepository
  responses: any = {}
  handleOpen(): void {
    this.getDetail()
    // console.log('abc', this.getDetail())
    // this.fullName = this.responses?.fullName
    // this.phone = this.responses?.phone
    // this.avatar = this.responses?.avatar
    // this.titleName = this.responses?.titleName
    // this.locationPathList = this.responses?.locationPathList
    // this.birthday = this.responses?.birthday
    // this.gender = this.responses?.gender
    // this.memberNo = this.responses?.memberNo
    // this.joinDate = this.responses?.joinDate
  }
  convertTimestampToDate(dateInput: Date): any {
    if (dateInput) {
      let timestamp = dateInput
      let date_not_formatted = new Date(timestamp)
      let formatted_string = '0' + date_not_formatted.getDate() + '/'
      if (date_not_formatted.getDate() < 10) {
        formatted_string += '0'
      }
      formatted_string += date_not_formatted.getMonth() + 1
      formatted_string += '/'
      if (date_not_formatted.getMonth() < 10) {
        // formatted_string += '0'
      }
      formatted_string += date_not_formatted.getFullYear()
      return formatted_string
    } else {
      return null
    }
  }
  getDetail(): void {
    this.api
      .getDetailEmployee(this.memberInfor?.id)
      .then((res: any) => {
        this.responses = res
      })
      .catch((err: any) => console.log(err))
  }
  handleClose(): void {
    this.formCertCode = false
  }
  handleSave(): void {
    // let a: any = this.$refs.dataInput
    this.apiupdateCertCode
      ?.updateCertCode(this.responses.id, { certCode: this.responses.certCode })
      .then(() => {
        this.$message.success('Cập nhật thành công')
        this.formCertCode = false
      })
      .catch(err => {
        this.$message.error(err.message)
      })
  }
  handleCancel() {
    this.formCertCode = false
    this.setOpenPopup({
      popupName: 'infoemployee-popup',
      isOpen: false
    })
  }

  created(): void {
    this.api = getRepository('employeeInfo', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiupdateCertCode = getRepository('employeeInfo', {
      districtId: this.user?.districts?.[0].id
    })
  }
  addicon(): void {
    this.formCertCode = true
  }
}
