




















































































































































































































import getRepository from '@/services'
import { Component, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import PermissionRoleRepository from '@/services/repositories/permissionRole'
import { namespace } from 'vuex-class'
import { ROLE_CODE, ROLE_TYPE } from '@/configs'
import { filter, find, debounce, trim, findIndex } from 'lodash'
const beUser = namespace('beUser')
import LocationRepository from '@/services/repositories/location'
import UserRepository from '@/services/repositories/user'

@Component
export default class UserPermission extends PopupMixin {
  @beUser.State('user') user!: any
  @Prop({ required: false, type: Object, default: {} }) memberInfor!: Record<string, any>
  @Prop() idr: any
  @Prop() typeCreate: any
  permissionMember: Record<string, any> = {}
  data: Record<string, any> = {}
  permissionRole: any = []
  loadingSubmit = false
  checkHO = true
  checkShowRole = true
  dataRole: Record<string, any> = []
  fullName = ''
  userId = ''
  apiPermission!: PermissionRoleRepository
  locationRes!: LocationRepository
  apiUser!: UserRepository

  objectIds: any = {
    transOfficeIdArray: [],
    areaIdArray: []
  }
  branchList: any = []
  listArea: any = []
  transactionList: any = []
  transactionOfficeList: any = []
  errorOffice = false
  errorArea = false
  query = {
    area: {
      i: 0,
      ps: 20,
      search: '',
      type: 1
    },
    offices: {
      i: 0,
      ps: 20,
      search: '',
      type: 3,
      parentId: ''
    }
  }

  //api dvkd
  loadMoreTransactionOffice(): void {
    this.query.offices.i += 20
    const a = debounce(this.getTransactionOffice, 100)
    a(this.query.offices.search, true)
  }

  remoteTransactionOffice(query: string): void {
    this.query.offices.search = trim(query)
    this.query.offices.i = 0
    this.query.offices.ps = 20
    const a = debounce(this.getTransactionOffice, 500)
    a(query)
  }

  async getTransactionOffice(search: string, isAdd = false): Promise<any> {
    try {
      const result = await this.locationRes?.list('office', {
        search: trim(search),
        i: this.query.offices.i,
        ps: this.query.offices.ps,
        parentId: this.memberInfor.branch.id
      })
      if (isAdd) {
        const a = result || []
        this.transactionList = [...this.transactionList, ...a]
      } else {
        this.transactionList = result || []
      }
    } catch (error) {
      this.transactionList = []
    }
  }
  //api khu vực
  remoteArea(query: string): void {
    this.query.area.search = trim(query)
    this.query.area.i = 0
    this.query.area.ps = 20
    const a = debounce(this.getAreaList, 500)
    a(query)
  }
  loadMoreArea(): void {
    this.query.area.i += this.query.area.ps
    const a = debounce(this.getAreaList, 100)
    a(this.query.area.search, true)
  }
  async getAreaList(search: string, isAdd = false): Promise<any> {
    try {
      const result = await this.locationRes?.list('area', {
        search: trim(search),
        i: this.query.area.i,
        ps: this.query.area.ps
      })
      if (isAdd) {
        const a = result || []
        this.listArea = [...this.listArea, ...a]
      } else {
        this.listArea = result || []
      }
    } catch (error) {
      console.log(error)
      this.listArea = []
    }
  }

  handleOpen(): void {
    this.errorOffice = false
    this.errorArea = false
    this.fullName = this.memberInfor?.fullName
    if (!this.memberInfor?.statusAccount) {
      this.userId = this.memberInfor?.id
    }
    this.permissionRole = []
    this.getListRole()
    this.getRoleOfMember()
    this.getAreaList('')
    this.getTransactionOffice('')
    this.query.offices.i = 0
    this.query.area.i = 0
  }
  handleChangeInput(type: string): void {
    console.log(type)

    if (type === 'validTransaction') {
      this.errorOffice = false
    }
    if (type === 'validArea') {
      this.errorArea = false
    }
    this.$forceUpdate()
  }

  getListRole(): void {
    this.apiPermission?.getPermissionRole().then((res: Record<string, any>) => {
      this.dataRole = res
    })
  }
  getRoleOfMember(): void {
    this.apiPermission?.getPermissionMember(this.memberInfor.id).then((res: Record<string, any>) => {
      this.permissionRole = res.map((item: any) => {
        return item.roleCode
      })
      res.forEach((item: any) => {
        if (item.roleCode == 'BRANCH_MEMBER') {
          this.objectIds.transOfficeIdArray = item.objectIds
        }
        if (item.roleCode == 'HO_MEMBER') {
          this.objectIds.areaIdArray = item.objectIds
        }
      })
    })
  }

  showPermission(code: keyof ROLE_TYPE): boolean {
    if (this.permissionRole.includes('BRANCH_MEMBER')) {
      this.checkShowRole = true
    } else {
      this.checkShowRole = false
      this.objectIds.transOfficeIdArray = ''
    }
    if (this.permissionRole.includes('HO_MEMBER')) {
      this.checkHO = true
    } else {
      this.checkHO = false
      this.objectIds.areaIdArray = ''
    }
    if (this.dataRole.length > 0) {
      const roleCode = ROLE_CODE[code]
      const checkShow = find(this.dataRole, item => item.code === roleCode)
      if (checkShow) {
        return true
      }
    }
    return false
  }

  handleClose(e: unknown): void {
    this.objectIds.transOfficeIdArray = ''
    this.objectIds.areaIdArray = ''
    console.log('e', e)
    this.userId = ''
    this.query.offices.i = 0
    this.query.area.i = 0
  }
  handleCreateUser(): void {
    const roles = this.dataRole.filter((item: any) => this.permissionRole.includes(item.code))
    const indexBranhMember = findIndex(roles, (role: Record<string, any>) => role.code == 'BRANCH_MEMBER')
    const indexHoMember = findIndex(roles, (role: Record<string, any>) => role.code == 'HO_MEMBER')
    if (indexBranhMember !== -1 && this.objectIds.transOfficeIdArray?.length) {
      roles[indexBranhMember].objectIds = [...this.objectIds.transOfficeIdArray]
    }
    if (indexHoMember !== -1 && this.objectIds.areaIdArray?.length) {
      roles[indexHoMember].objectIds = [...this.objectIds.areaIdArray]
    }
    this.loadingSubmit = true
    // const roles = this.dataRole.filter((item: any) => this.permissionRole.includes(item.code))
    const params = {
      memberId: this.memberInfor.id,
      permissionRoles: roles
    }
    this.apiPermission
      .createUser(params)
      .then(() => {
        this.loadingSubmit = false
        this.setOpenPopup({
          popupName: 'userPermission',
          isOpen: false
        })
        this.$message.success('Tạo tài khoản thành công!')
        this.$emit('save')
      })
      .catch((err: any) => this.$message.error(err.message))
  }
  handleSave(): void {
    if (this.checkShowRole === true) {
      if (trim(this.objectIds.transOfficeIdArray) === '') {
        this.errorOffice = true
        return
      }
    }
    if (this.checkHO === true) {
      if (trim(this.objectIds.areaIdArray) === '') {
        this.errorArea = true
        return
      }
    }

    const roles = this.dataRole.filter((item: any) => this.permissionRole.includes(item.code))
    const indexBranhMember = findIndex(roles, (role: Record<string, any>) => role.code == 'BRANCH_MEMBER')
    const indexHoMember = findIndex(roles, (role: Record<string, any>) => role.code == 'HO_MEMBER')
    if (indexBranhMember !== -1 && this.objectIds.transOfficeIdArray?.length) {
      roles[indexBranhMember].objectIds = [...this.objectIds.transOfficeIdArray]
    }
    if (indexHoMember !== -1 && this.objectIds.areaIdArray?.length) {
      roles[indexHoMember].objectIds = [...this.objectIds.areaIdArray]
    }
    console.log('role',roles)
    const params = {
      memberId: this.memberInfor.id,
      permissionRoles: roles
    }

    if (this.typeCreate == 'create') {
      this.apiPermission
        .createUser(params)
        .then(() => {
          this.$message.success('Tạo tài khoản thành công !')
          this.setOpenPopup({
            popupName: 'userPermission',
            isOpen: false
          })
        })
        .catch((err: any) => this.$message.success(err.message))
    } else {
      this.apiPermission
        .updatePermissionMember(params)
        .then(() => {
          this.$message.success('Cập nhật quyền thành công !')
          this.setOpenPopup({
            popupName: 'userPermission',
            isOpen: false
          })
        })
        .catch((err: any) => this.$message.success(err.message))
    }
  }
  created(): void {
    this.apiPermission = getRepository('permissionRole', {
      districtId: this.user?.districts?.[0].id
    })
    this.locationRes = getRepository('location', {
      districtId: this.districtId
    })
    // this.apiUser = getRepository('user', {
    //   districtId: this.user?.districts?.[0].id
    // })
  }
}
